/* Responsive styles are already defined in .main-container section above */

/* ========================================== */
/* MEDIA PREVIEW STYLES */
/* ========================================== */

.media-preview {
  margin-top: 18px;
  padding: 18px;
  background: rgba(30, 41, 59, 0.4);
  border: 1px solid rgba(148, 163, 184, 0.2);
  border-radius: 12px;
  position: relative;
  backdrop-filter: blur(10px);
}

.preview-image {
  width: 100%;
  height: auto;
  display: block;
  margin-bottom: 14px;
  border: 1px solid rgba(148, 163, 184, 0.2);
  border-radius: 8px;
}

.preview-image.stream {
  border: 1px solid rgba(239, 68, 68, 0.5);
  animation: streamBorder 1.5s infinite;
  transform: scaleX(-1);
  border-radius: 8px;
}

@keyframes streamBorder {
  0%, 100% {
    border-color: rgba(239, 68, 68, 0.5);
    box-shadow: 0 0 16px rgba(239, 68, 68, 0.3);
  }
  50% {
    border-color: rgba(239, 68, 68, 0.8);
    box-shadow: 0 0 24px rgba(239, 68, 68, 0.5);
  }
}

.preview-video {
  width: 100%;
  max-width: 800px;
  height: auto;
  display: block;
  margin: 0 auto 20px;
  background: rgba(15, 23, 42, 0.8);
  border: 2px solid rgba(148, 163, 184, 0.3);
  border-radius: 12px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

.preview-audio {
  width: 100%;
  max-width: 500px;
  display: block;
  margin: 16px auto;
  border-radius: 8px;
}

/* Audio Visualizer */
.audio-visualizer {
  margin-top: 20px;
  padding: 24px;
  background: rgba(30, 41, 59, 0.5);
  border: 1px solid rgba(168, 85, 247, 0.3);
  border-radius: 12px;
  text-align: center;
  position: relative;
}

.audio-visualizer p {
  color: #94a3b8;
  margin-top: 16px;
  font-size: 14px;
}

.audio-wave {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 4px;
  height: 50px;
  margin-top: 16px;
}

.audio-wave span {
  width: 6px;
  background: linear-gradient(180deg, #a855f7 0%, #7c3aed 100%);
  border-radius: 3px;
  animation: audioWave 0.8s ease-in-out infinite;
}

.audio-wave span:nth-child(1) { animation-delay: 0s; height: 20px; }
.audio-wave span:nth-child(2) { animation-delay: 0.1s; height: 35px; }
.audio-wave span:nth-child(3) { animation-delay: 0.2s; height: 50px; }
.audio-wave span:nth-child(4) { animation-delay: 0.3s; height: 35px; }
.audio-wave span:nth-child(5) { animation-delay: 0.4s; height: 20px; }

.audio-wave.recording span {
  background: linear-gradient(180deg, #ef4444 0%, #dc2626 100%);
}

@keyframes audioWave {
  0%, 100% { transform: scaleY(0.3); }
  50% { transform: scaleY(1); }
}

.audio-preview {
  border-color: rgba(168, 85, 247, 0.3);
}

.media-preview {
  margin-top: 20px;
  padding: 24px;
  background: rgba(30, 41, 59, 0.5);
  border: 1px solid rgba(148, 163, 184, 0.2);
  border-radius: 12px;
  backdrop-filter: blur(10px);
}

.media-preview h4 {
  margin: 0 0 16px 0;
  color: #e2e8f0;
  font-size: 16px;
  font-weight: 600;
  text-align: center;
}

.preview-actions {
  display: flex;
  gap: 16px;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

/* Container query for preview actions */
@container control-panel (max-width: 650px) {
  .preview-actions {
    flex-direction: column;
  }
  
  .preview-actions .btn {
    width: 100%;
  }
  
  .media-preview img,
  .media-preview video {
    max-width: 100%;
  }
}

.stream-badge {
  position: absolute;
  top: 28px;
  right: 28px;
  background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
  color: #fff;
  padding: 8px 16px;
  font-weight: 700;
  font-size: 12px;
  border-radius: 8px;
  box-shadow: 0 4px 16px rgba(239, 68, 68, 0.5);
  animation: pulse 2s infinite;
  z-index: 10;
  letter-spacing: 1px;
}

.loading-placeholder {
  text-align: center;
  padding: 60px 24px;
  color: #64748b;
}

.spinner {
  border: 3px solid rgba(148, 163, 184, 0.2);
  border-top: 3px solid #3b82f6;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  animation: spin 1s linear infinite;
  margin: 0 auto 18px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loading-placeholder p {
  margin: 0;
  font-size: 14px;
  font-weight: 500;
  color: #94a3b8;
}

/* ========================================== */
/* RECORDING & PROGRESS STYLES */
/* ========================================== */

.recording-controls {
  display: flex;
  align-items: center;
  gap: 16px;
  flex-wrap: wrap;
}

.duration-input-group {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px 16px;
  background: rgba(71, 85, 105, 0.2);
  border: 1px solid rgba(148, 163, 184, 0.2);
  border-radius: 10px;
}

.duration-input-group label {
  color: #cbd5e1;
  font-weight: 600;
  font-size: 14px;
  white-space: nowrap;
}

.duration-input {
  width: 80px;
  padding: 8px 12px;
  background: rgba(15, 23, 42, 0.6);
  border: 1px solid rgba(148, 163, 184, 0.3);
  border-radius: 8px;
  color: #e2e8f0;
  font-size: 14px;
  font-weight: 600;
  text-align: center;
  font-family: 'JetBrains Mono', monospace;
  transition: all 0.3s ease;
}

.duration-input:hover:not(:disabled) {
  border-color: rgba(59, 130, 246, 0.5);
  background: rgba(15, 23, 42, 0.8);
}

.duration-input:focus {
  outline: none;
  border-color: rgba(59, 130, 246, 0.7);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.duration-input:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Container query for recording controls */
@container control-panel (max-width: 700px) {
  .recording-controls {
    flex-direction: column;
    align-items: stretch;
  }
  
  .duration-input-group {
    justify-content: center;
  }
  
  .recording-controls .btn {
    width: 100%;
  }
}

.recording-progress {
  margin-top: 14px;
  padding: 16px;
