  overflow: hidden;
  margin-bottom: 10px;
  border: 1px solid rgba(148, 163, 184, 0.2);
  border-radius: 6px;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #ef4444 0%, #dc2626 100%);
  transition: width 0.3s ease;
  box-shadow: 0 0 16px rgba(239, 68, 68, 0.5);
