/* ============================================
   KEYLOGGER DUAL PANEL STYLES
   ============================================ */

.keylog-display-dual {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin-top: 20px;
}

.keylog-panel {
  background: rgba(15, 23, 42, 0.6);
  border: 1px solid rgba(148, 163, 184, 0.2);
  border-radius: 12px;
  overflow: hidden;
  backdrop-filter: blur(10px);
}

.keylog-panel h4 {
  margin: 0;
  padding: 14px 18px;
  background: rgba(30, 41, 59, 0.8);
  border-bottom: 1px solid rgba(148, 163, 184, 0.2);
  color: #e2e8f0;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0.3px;
}

.keylog-panel .keylog-content {
  padding: 16px;
  height: 400px;
  overflow-y: auto;
  overflow-x: hidden;
}

.keylog-panel .keylog-content pre {
  margin: 0;
  font-family: 'JetBrains Mono', monospace;
  font-size: 13px;
  line-height: 1.8;
  color: #cbd5e1;
  white-space: pre-wrap;
  word-wrap: break-word;
  word-break: break-all;
}

/* Scrollbar styling for keylog panels */
.keylog-panel .keylog-content::-webkit-scrollbar {
  width: 8px;
}

.keylog-panel .keylog-content::-webkit-scrollbar-track {
  background: rgba(15, 23, 42, 0.3);
  border-radius: 4px;
}

.keylog-panel .keylog-content::-webkit-scrollbar-thumb {
  background: rgba(148, 163, 184, 0.3);
  border-radius: 4px;
}

.keylog-panel .keylog-content::-webkit-scrollbar-thumb:hover {
  background: rgba(148, 163, 184, 0.5);
}

/* Keylog mode selector */
.keylog-mode-selector {
  margin-bottom: 20px;
  padding: 16px;
  background: rgba(30, 41, 59, 0.5);
  border: 1px solid rgba(148, 163, 184, 0.2);
  border-radius: 12px;
}

.keylog-mode-selector label {
  display: block;
  margin-bottom: 10px;
  color: #e2e8f0;
  font-weight: 600;
  font-size: 14px;
}

.mode-buttons {
  display: flex;
  gap: 12px;
  margin-bottom: 12px;
}

.btn-mode {
  flex: 1;
  padding: 12px 20px;
  background: rgba(15, 23, 42, 0.6);
  color: #cbd5e1;
  border: 1px solid rgba(148, 163, 184, 0.2);
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn-mode:hover {
  background: rgba(30, 41, 59, 0.8);
  border-color: rgba(148, 163, 184, 0.4);
  transform: translateY(-2px);
}

.btn-mode.active {
  background: linear-gradient(135deg, rgba(59, 130, 246, 0.2) 0%, rgba(37, 99, 235, 0.2) 100%);
  color: #60a5fa;
  border-color: rgba(59, 130, 246, 0.5);
  box-shadow: 0 4px 16px rgba(59, 130, 246, 0.2);
}

.mode-description {
  margin: 0;
  color: #94a3b8;
  font-size: 13px;
  font-style: italic;
}

/* Responsive design for dual panel */
@media (max-width: 1200px) {
  .keylog-display-dual {
    grid-template-columns: 1fr;
  }
}

/* ============================================
   CUSTOM MODAL STYLES
   ============================================ */

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.75);
  backdrop-filter: blur(8px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  animation: fadeIn 0.2s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.modal-box {
  background: linear-gradient(135deg, rgba(30, 41, 59, 0.98) 0%, rgba(51, 65, 85, 0.98) 100%);
  border: 1px solid rgba(148, 163, 184, 0.3);
  border-radius: 16px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
  min-width: 400px;
  max-width: 500px;
  animation: slideIn 0.3s ease;
  overflow: hidden;
}

@keyframes slideIn {
  from {
    transform: scale(0.9) translateY(-20px);
    opacity: 0;
  }
  to {
    transform: scale(1) translateY(0);
    opacity: 1;
  }
}

.modal-header {
  padding: 20px 24px;
  background: rgba(15, 23, 42, 0.8);
  border-bottom: 1px solid rgba(148, 163, 184, 0.2);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.modal-header h3 {
