  font-size: 13px;
  padding: 6px 0;
  border-bottom: 1px solid rgba(148, 163, 184, 0.1);
}

.control-info-panel li:last-child {
  border-bottom: none;
}

.stealth-warning {
  margin-top: 16px;
  padding: 12px 16px;
  background: rgba(251, 191, 36, 0.15);
  border: 1px solid rgba(251, 191, 36, 0.4);
  border-radius: 8px;
  color: #fcd34d;
  font-size: 13px;
  line-height: 1.6;
}

.stealth-warning strong {
  font-weight: 700;
  color: #fbbf24;
}

/* Monitor selection styles */
.camera-radio-item {
  transition: all 0.3s ease;
}

.camera-radio-item:hover {
  background: rgba(236, 72, 153, 0.05);
}

.camera-radio-item input:checked + .camera-info {
  color: #ec4899;
}

/* Button variations for remote control */
.btn-success {
  background: linear-gradient(135deg, rgba(34, 197, 94, 0.2) 0%, rgba(22, 163, 74, 0.2) 100%);
  color: #4ade80;
  border: 1px solid rgba(34, 197, 94, 0.4);
}

.btn-success:hover:not(:disabled) {
  background: linear-gradient(135deg, rgba(34, 197, 94, 0.3) 0%, rgba(22, 163, 74, 0.3) 100%);
  border-color: rgba(34, 197, 94, 0.6);
  box-shadow: 0 4px 12px rgba(34, 197, 94, 0.3);
}

.btn-danger {
  background: linear-gradient(135deg, rgba(239, 68, 68, 0.2) 0%, rgba(220, 38, 38, 0.2) 100%);
  color: #fca5a5;
  border: 1px solid rgba(239, 68, 68, 0.4);
}

.btn-danger:hover:not(:disabled) {
  background: linear-gradient(135deg, rgba(239, 68, 68, 0.3) 0%, rgba(220, 38, 38, 0.3) 100%);
  border-color: rgba(239, 68, 68, 0.6);
  box-shadow: 0 4px 12px rgba(239, 68, 68, 0.3);
}

.btn-warning {
  background: linear-gradient(135deg, rgba(251, 191, 36, 0.2) 0%, rgba(245, 158, 11, 0.2) 100%);
  color: #fcd34d;
  border: 1px solid rgba(251, 191, 36, 0.4);
}

.btn-warning:hover:not(:disabled) {
  background: linear-gradient(135deg, rgba(251, 191, 36, 0.3) 0%, rgba(245, 158, 11, 0.3) 100%);
  border-color: rgba(251, 191, 36, 0.6);
  box-shadow: 0 4px 12px rgba(251, 191, 36, 0.3);
}

/* Responsive adjustments */
@media (max-width: 1200px) {
  .screen-stream-container canvas {
    max-width: 90%;
  }
  
  .control-buttons {
    flex-direction: column;
  }
  
  .control-buttons button {
    width: 100%;
  }
}

/* ============================================
   REMOTE CONTROL INTERFACE - NEW DESIGN
   ============================================ */

.remote-control-interface {
  display: flex;
  flex-direction: column;
  height: calc(100vh - 100px);
  background: #000;
  border-radius: 12px;
  overflow: hidden;
  border: 1px solid rgba(148, 163, 184, 0.2);
}

.remote-control-interface.fullscreen-mode {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100vh;
  border-radius: 0;
  z-index: 9999;
}

.rc-control-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 20px;
  background: linear-gradient(135deg, rgba(15, 23, 42, 0.98) 0%, rgba(30, 41, 59, 0.98) 100%);
  border-bottom: 1px solid rgba(148, 163, 184, 0.2);
  backdrop-filter: blur(20px);
  gap: 20px;
  flex-wrap: wrap;
}

.rc-control-left, .rc-control-right {
  display: flex;
  align-items: center;
  gap: 12px;
  flex-wrap: wrap;
}

.rc-status-badges {
  display: flex;
  gap: 8px;
  align-items: center;
}

.rc-badge {
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 600;
  font-family: 'JetBrains Mono', monospace;
  white-space: nowrap;
}

.rc-badge-live {
  background: linear-gradient(135deg, rgba(239, 68, 68, 0.3) 0%, rgba(220, 38, 38, 0.3) 100%);
  color: #fca5a5;
  border: 1px solid rgba(239, 68, 68, 0.5);
  animation: pulse-glow 2s infinite;
}

.rc-badge-info {
  background: rgba(59, 130, 246, 0.15);
  color: #93c5fd;
  border: 1px solid rgba(59, 130, 246, 0.3);
}

@keyframes pulse-glow {
  0%, 100% {
    box-shadow: 0 0 8px rgba(239, 68, 68, 0.4);
  }
  50% {
    box-shadow: 0 0 16px rgba(239, 68, 68, 0.6);
  }
}

.rc-checkbox-label {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  background: rgba(30, 41, 59, 0.8);
  border: 1px solid rgba(148, 163, 184, 0.3);
  border-radius: 6px;
  color: #cbd5e1;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  user-select: none;
  transition: all 0.2s ease;
}

.rc-checkbox-label:hover {
  background: rgba(30, 41, 59, 1);
  border-color: rgba(148, 163, 184, 0.5);
  transform: translateY(-1px);
}

.rc-checkbox-label input[type="checkbox"] {
  cursor: pointer;
  width: 16px;
  height: 16px;
}

.rc-scale-select {
  padding: 8px 16px;
  background: rgba(30, 41, 59, 0.8);
  border: 1px solid rgba(148, 163, 184, 0.3);
  border-radius: 6px;
  color: #cbd5e1;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  outline: none;
  transition: all 0.2s ease;
  min-width: 130px;
}

.rc-scale-select:hover {
  background: rgba(30, 41, 59, 1);
  border-color: rgba(148, 163, 184, 0.5);
  transform: translateY(-1px);
}

.rc-scale-select:focus {
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.2);
}

.rc-screen-container {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #000;
  position: relative;
  overflow: hidden;
  padding: 20px;
}

.rc-screen-container.rc-active {
  background: radial-gradient(circle at center, rgba(236, 72, 153, 0.05) 0%, #000 100%);
}

.rc-canvas {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  border-radius: 8px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.8);
  transition: all 0.3s ease;
}

.rc-canvas.fit {
  object-fit: contain;
}

.rc-canvas.fill {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.rc-canvas.actual {
  object-fit: none;
  max-width: none;
  max-height: none;
}

.rc-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #64748b;
}

.rc-loading .spinner {
  margin-bottom: 20px;
}

.rc-loading p {
  font-size: 14px;
  color: #94a3b8;
}

.rc-status-overlay {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
  pointer-events: none;
  z-index: 10;
}

.rc-status-indicator {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 12px 24px;
  background: rgba(236, 72, 153, 0.95);
  border: 2px solid rgba(236, 72, 153, 1);
  border-radius: 12px;
  backdrop-filter: blur(20px);
  box-shadow: 0 8px 32px rgba(236, 72, 153, 0.5);
  color: #fff;
  font-size: 14px;
  font-weight: 700;
  animation: slideInDown 0.5s ease;
}

.rc-pulse {
  width: 12px;
  height: 12px;
  background: #fff;
  border-radius: 50%;
  animation: pulse-dot 1.5s infinite;
}

@keyframes pulse-dot {
  0%, 100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.5;
    transform: scale(1.2);
  }
}

.rc-stealth-indicator {
  padding: 10px 20px;
  background: rgba(251, 191, 36, 0.95);
  border: 2px solid rgba(251, 191, 36, 1);
  border-radius: 10px;
  backdrop-filter: blur(20px);
  box-shadow: 0 8px 32px rgba(251, 191, 36, 0.5);
  color: #000;
  font-size: 13px;
  font-weight: 600;
  animation: slideInDown 0.5s ease 0.2s both;
}

@keyframes slideInDown {
  from {
    opacity: 0;
    transform: translateX(-50%) translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateX(-50%) translateY(0);
  }
}

.rc-help-overlay {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  padding: 10px 20px;
  background: rgba(15, 23, 42, 0.9);
  border: 1px solid rgba(148, 163, 184, 0.3);
  border-radius: 8px;
  color: #94a3b8;
  font-size: 13px;
  backdrop-filter: blur(10px);
  animation: fadeIn 0.5s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Responsive adjustments for remote control */
@media (max-width: 768px) {
  .rc-control-bar {
    flex-direction: column;
    align-items: stretch;
  }
  
  .rc-control-left, .rc-control-right {
    width: 100%;
    justify-content: space-between;
  }
  
  .rc-status-badges {
    flex-wrap: wrap;
  }
  
  .rc-screen-container {
    padding: 10px;
  }
}

/* ========================================== */
/* INSTALLED APPLICATIONS STYLES */
/* ========================================== */

.apps-list-container {
  margin-top: 18px;
  background: rgba(30, 41, 59, 0.4);
  border: 1px solid rgba(148, 163, 184, 0.2);
  border-radius: 12px;
  overflow: hidden;
  backdrop-filter: blur(10px);
}

.apps-list-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  background: rgba(15, 23, 42, 0.6);
  border-bottom: 1px solid rgba(148, 163, 184, 0.1);
  font-weight: 700;
  color: #ffffff;
  font-size: 14px;
  gap: 16px;
  flex-wrap: wrap;
}

.apps-header-left {
  display: flex;
  align-items: center;
  gap: 12px;
}

.apps-header-right {
  display: flex;
  align-items: center;
  gap: 12px;
  flex-wrap: wrap;
}

.filter-select {
  padding: 8px 14px;
  background: rgba(15, 23, 42, 0.6);
  border: 1px solid rgba(148, 163, 184, 0.2);
  border-radius: 8px;
  color: #e2e8f0;
  font-size: 13px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.filter-select:focus {
  outline: none;
  border-color: rgba(59, 130, 246, 0.5);
  background: rgba(15, 23, 42, 0.8);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.apps-search-input {
  padding: 8px 14px;
  background: rgba(15, 23, 42, 0.6);
  border: 1px solid rgba(148, 163, 184, 0.2);
  border-radius: 8px;
  color: #e2e8f0;
  font-size: 13px;
  width: 250px;
  transition: all 0.3s ease;
}

.apps-search-input::placeholder {
  color: #64748b;
}

.apps-search-input:focus {
  outline: none;
  border-color: rgba(59, 130, 246, 0.5);
  background: rgba(15, 23, 42, 0.8);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.apps-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 16px;
  padding: 20px;
  background: rgba(15, 23, 42, 0.3);
  max-height: 600px;
  overflow-y: auto;
}

.app-card {
  background: rgba(30, 41, 59, 0.5);
  border: 1px solid rgba(148, 163, 184, 0.15);
  border-radius: 12px;
  padding: 14px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  display: flex;
  flex-direction: column;
  gap: 0;
  height: 200px;
  min-height: 200px;
  max-height: 200px;
  overflow: hidden;
}

.app-card:hover {
  border-color: rgba(59, 130, 246, 0.4);
  box-shadow: 0 4px 20px rgba(59, 130, 246, 0.2);
  transform: translateY(-3px);
  background: rgba(30, 41, 59, 0.7);
}

.app-card.running {
  border-color: rgba(34, 197, 94, 0.3);
}

.app-card.running:hover {
  border-color: rgba(34, 197, 94, 0.5);
  box-shadow: 0 4px 20px rgba(34, 197, 94, 0.2);
}

.app-card.stopped {
  border-color: rgba(100, 116, 139, 0.3);
}

.app-card-header {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  min-width: 0;
  flex-shrink: 0;
  margin-bottom: 8px;
}

.app-card-content {
  flex: 1;
  min-height: 0;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding-right: 4px;
  margin-bottom: 10px;
}

.app-card-content::-webkit-scrollbar {
  width: 4px;
}

.app-card-content::-webkit-scrollbar-track {
  background: rgba(15, 23, 42, 0.3);
  border-radius: 2px;
}

.app-card-content::-webkit-scrollbar-thumb {
  background: rgba(71, 85, 105, 0.6);
  border-radius: 2px;
}

.app-card-content::-webkit-scrollbar-thumb:hover {
  background: rgba(71, 85, 105, 0.8);
}

.app-status-indicator {
  font-size: 12px;
  margin-top: 2px;
  flex-shrink: 0;
}

.app-name-section {
  flex: 1;
  min-width: 0;
}

.app-name {
  font-size: 14px;
  font-weight: 600;
  color: #f1f5f9;
  margin: 0 0 3px 0;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  line-height: 1.3;
}

.app-version {
  display: inline-block;
  padding: 2px 8px;
  background: rgba(59, 130, 246, 0.15);
  color: #93c5fd;
  border: 1px solid rgba(59, 130, 246, 0.3);
  border-radius: 6px;
  font-size: 11px;
  font-weight: 600;
  font-family: 'JetBrains Mono', monospace;
}

.app-running-info {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 5px 8px;
  background: rgba(34, 197, 94, 0.1);
  border: 1px solid rgba(34, 197, 94, 0.2);
  border-radius: 6px;
  flex-shrink: 0;
  min-width: 0;
}

.pid-count {
  color: #86efac;
  font-size: 12px;
  font-weight: 600;
  font-family: 'JetBrains Mono', monospace;
}

.app-location {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 6px 8px;
  background: rgba(15, 23, 42, 0.5);
  border: 1px solid rgba(148, 163, 184, 0.1);
  border-radius: 6px;
  min-width: 0;
  overflow: hidden;
  flex-shrink: 1;
}

.location-icon {
  flex-shrink: 0;
  font-size: 14px;
}

.location-path {
  color: #94a3b8;
  font-size: 11px;
  font-family: 'JetBrains Mono', monospace;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  min-width: 0;
  flex: 1;
}

.app-actions {
  display: flex;
  gap: 8px;
  flex-shrink: 0;
  margin-top: 0;
}

.btn-start-app,
.btn-stop-app {
  flex: 1;
  padding: 8px 12px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 4px;
}

.btn-start-app {
  background: rgba(34, 197, 94, 0.15);
  color: #86efac;
  border-color: rgba(34, 197, 94, 0.3);
}

.btn-start-app:hover {
  background: rgba(34, 197, 94, 0.25);
  border-color: rgba(34, 197, 94, 0.5);
  box-shadow: 0 4px 16px rgba(34, 197, 94, 0.3);
  transform: translateY(-2px);
}

.btn-stop-app {
  background: rgba(239, 68, 68, 0.15);
  color: #fca5a5;
  border-color: rgba(239, 68, 68, 0.3);
}

.btn-stop-app:hover {
  background: rgba(239, 68, 68, 0.25);
  border-color: rgba(239, 68, 68, 0.5);
  box-shadow: 0 4px 16px rgba(239, 68, 68, 0.3);
  transform: translateY(-2px);
}

.no-apps-message {
  padding: 40px;
  text-align: center;
  color: #94a3b8;
  font-size: 14px;
}

.no-apps-message p {
  margin: 0;
}

.process-memory {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 4px 10px;
  background: rgba(59, 130, 246, 0.15);
  border: 1px solid rgba(59, 130, 246, 0.3);
  border-radius: 8px;
  color: #93c5fd;
  font-size: 12px;
  font-weight: 600;
  font-family: 'JetBrains Mono', monospace;
  white-space: nowrap;
}

.pid-memory {
  color: #94a3b8;
  font-size: 11px;
  font-family: 'JetBrains Mono', monospace;
  margin-left: auto;
  padding-left: 10px;
}

/* Responsive for apps grid */
@media (max-width: 1200px) {
  .apps-grid {
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  }
}

@media (max-width: 768px) {
  .apps-grid {
    grid-template-columns: 1fr;
  }
  
  .apps-header-right {
    width: 100%;
  }
  
  .apps-search-input {
    width: 100%;
  }
