ontent: space-between;\r\n  align-items: center;\r\n  backdrop-filter: blur(20px);\r\n  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.3);\r\n  gap: 20px;\r\n  min-width: 0;\r\n}\r\n\r\n.header h1 {\r\n  color: #ffffff;\r\n  font-size: 20px;\r\n  font-weight: 700;\r\n  letter-spacing: -0.5px;\r\n  white-space: nowrap;\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n  min-width: 0;\r\n  flex: 1 1 auto;\r\n  max-width: fit-content;\r\n}\r\n\r\n.header-actions {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 15px;\r\n  flex-wrap: nowrap;\r\n  flex-shrink: 0;\r\n}\r\n\r\n.connection-info {\r\n  display: flex;\r\n  align-items: center;\r\n}\r\n\r\n.connection-badge {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 10px;\r\n  padding: 10px 18px;\r\n  background: rgba(15, 23, 42, 0.6);\r\n  border: 1px solid rgba(148, 163, 184, 0.2);\r\n  border-radius: 10px;\r\n  font-weight: 600;\r\n  font-size: 14px;\r\n  color: #cbd5e1;\r\n  backdrop-filter: blur(10px);\r\n  font-family: 'JetBrains Mono', monospace;\r\n  white-space: nowrap;\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n  min-width: 0;\r\n}\r\n\r\n.btn-logout {\r\n  padding: 10px 24px;\r\n  background: rgba(239, 68, 68, 0.15);\r\n  color: #fca5a5;\r\n  border: 1px solid rgba(239, 68, 68, 0.3);\r\n  border-radius: 10px;\r\n  font-size: 14px;\r\n  font-weight: 600;\r\n  cursor: pointer;\r\n  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);\r\n}\r\n\r\n.btn-logout:hover {\r\n  background: rgba(239, 68, 68, 0.25);\r\n  border-color: rgba(239, 68, 68, 0.5);\r\n  box-shadow: 0 4px 20px rgba(239, 68, 68, 0.2);\r\n  transform: translateY(-2px);\r\n}\r\n\r\n/* Persistence and Kill Server buttons */\r\n.btn-persist-on {\r\n  padding: 10px 28px !important;\r\n  min-width: 160px;\r\n  background: rgba(34, 197, 94, 0.15) !important;\r\n  color: #4ade80 !important;\r\n  border: 1px solid rgba(34, 197, 94, 0.4) !important;\r\n  border-radius: 10px;\r\n  font-size: 14px;\r\n  font-weight: 600;\r\n  cursor: