  font-weight: 600;\r\n  font-size: 14px;\r\n}\r\n\r\n.status-indicator {\r\n  width: 12px;\r\n  height: 12px;\r\n  border-radius: 50%;\r\n  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;\r\n}\r\n\r\n.status-indicator.connected {\r\n  background-color: #10b981;\r\n  box-shadow: 0 0 16px rgba(16, 185, 129, 0.6), 0 0 32px rgba(16, 185, 129, 0.3);\r\n}\r\n\r\n.status-indicator.disconnected {\r\n  background-color: #ef4444;\r\n  box-shadow: 0 0 16px rgba(239, 68, 68, 0.6), 0 0 32px rgba(239, 68, 68, 0.3);\r\n}\r\n\r\n@keyframes pulse {\r\n  0%, 100% {\r\n    opacity: 1;\r\n  }\r\n  50% {\r\n    opacity: 0.5;\r\n  }\r\n}\r\n\r\n/* Main Container - Resizable Layout */\r\n.main-container {\r\n  display: flex;\r\n  gap: 0;\r\n  height: calc(100vh - 61px);\r\n  background: transparent;\r\n  position: relative;\r\n}\r\n\r\n@media (max-width: 1024px) {\r\n  .main-container {\r\n    flex-direction: column;\r\n  }\r\n  \r\n  .logs-panel {\r\n    max-height: 300px;\r\n    width: 100% !important;\r\n  }\r\n  \r\n  .resize-handle {\r\n    display: none;\r\n  }\r\n  \r\n  .header {\r\n    padding: 15px 20px;\r\n    gap: 10px;\r\n  }\r\n  \r\n  .header h1 {\r\n    font-size: 16px;\r\n  }\r\n  \r\n  .header-actions {\r\n    gap: 8px;\r\n  }\r\n  \r\n  .connection-badge {\r\n    padding: 8px 12px;\r\n    font-size: 12px;\r\n  }\r\n  \r\n  /* Hide button text on small screens, keep only icons */\r\n  .btn-text {\r\n    display: none;\r\n  }\r\n  \r\n  .btn-logout,\r\n  .btn-persist-on,\r\n  .btn-persist-off,\r\n  .btn-kill-server {\r\n    padding: 10px 12px !important;\r\n    font-size: 18px !important;\r\n    min-width: auto !important;\r\n  }\r\n}\r\n\r\n/* Medium screens - hide text on smaller windows */\r\n@media (max-width: 1280px) {\r\n  .btn-text {\r\n    display: none;\r\n  }\r\n  \r\n  .btn-logout,\r\n  .btn-persist-on,\r\n  .btn-persist-off,\r\n  .btn-kill-server {\r\n    padding: 10px 16px !important;\r\n    font-size: 16px !important;\r\n    min-width: auto !important;\r\n  }\r\n}\r\n\r\n/* Buttons */\r\n.button-group {\r\n  display: flex;\r\n  gap: 10px;\r\n}\r\n\r\n.btn {\r\n  padding: 12px 24px;\r\n  border: 1px solid rgba(148, 163, 184, 0.2);\r\n  border-radius: 10px;\r\n  font-size: 14px;\r\n  font-weight: 600;\r\n  cursor: pointer;\r\n  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);\r\n  flex: 1;\r\n  background: rgba(71, 85, 105, 0.3);\r\n  color: #e2e8f0;\r\n}\r\n\r\n.btn:hover:not(:disabled) {\r\n  background: rgba(71, 85, 105, 0.5);\r\n  border-color: rgba(148, 163, 184, 0.4);\r\n  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);\r\n  transform: translateY(-2px);\r\n}\r\n\r\n.btn:disabled {\r\n  opacity: 0.4;\r\n  cursor: not-allowed;\r\n  border-color: rgba(100, 116, 139, 0.2);\r\n  color: #64748b;\r\n}\r\n\r\n.btn-connect {\r\n  background: linear-gradient(135deg, rgba(16, 185, 129, 0.2) 0%, rgba(5, 150, 105, 0.2) 100%);\r\n  color: #6ee7b7;\r\n  border: 1px solid rgba(16, 185, 129, 0.3);\r\n}\r\n\r\n.btn-connect:hover {\r\n  background: linear-gradient(135deg, rgba(16, 185, 129, 0.3) 0%, rgba(5, 150, 105, 0.3) 100%);\r\n  border-color: rgba(16, 185, 129, 0.5);\r\n  box-shadow: 0 4px 20px rgba(16, 185, 129, 0.3);\r\n}\r\n\r\n.btn-disconnect {\r\n  background: rgba(239, 68, 68, 0.15);\r\n  color: #fca5a5;\r\n  border: 1px solid rgba(239, 68, 68, 0.3);\r\n}\r\n\r\n.btn-disconnect:hover {\r\n  background: rgba(239, 68, 68, 0.25);\r\n  border-color: rgba(239, 68, 68, 0.5);\r\n  box-shadow: 0 4px 20px rgba(239, 68, 68, 0.2);\r\n}\r\n\r\n.btn-command {\r\n  background: rgba(59, 130, 246, 0.15);\r\n  color: #93c5fd;\r\n  border: 1px solid rgba(59, 130, 246, 0.3);\r\n  border-radius: 10px;\r\n  padding: 12px 20px;\r\n  margin: 6px;\r\n  font-size: 14px;\r\n  font-weight: 600;\r\n  width: auto;\r\n  max-width: fit-content;\r\n  flex-shrink: 0;\r\n  cursor: pointer;\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.btn-command:hover:not(:disabled) {\r\n  background: rgba(59, 130, 246, 0.25);\r\n  border-color: rgba(59, 130, 246, 0.5);\r\n  box-shadow: 0 4px 20px rgba(59, 130, 246, 0.3);\r\n  transform: translateY(-2px);\r\n}\r\n\r\n.btn-danger {\r\n  background: rgba(239, 68, 68, 0.15);\r\n  color: #fca5a5;\r\n  border: 1px solid rgba(239, 68, 68, 0.3);\r\n  border-radius: 10px;\r\n  padding: 12px 20px;\r\n  margin: 6px;\r\n  font-size: 14px;\r\n  font-weight: 600;\r\n  width: auto;\r\n  max-width: fit-content;\r\n  flex-shrink: 0;\r\n  cursor: pointer;\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.btn-danger:hover:not(:disabled) {\r\n  background: rgba(239, 68, 68, 0.25);\r\n  border-color: rgba(239, 68, 68, 0.5);\r\n  box-shadow: 0 4px 20px rgba(239, 68, 68, 0.3);\r\n  transform: translateY(-2px);\r\n}\r\n\r\n.btn-small {\r\n  padding: 12px 14px;\r\n  font-size: 13px;\r\n  background: rgba(71, 85, 105, 0.3);\r\n  color: #94a3b8;\r\n  border: 1px solid rgba(148, 163, 184, 0.2);\r\n  border-radius: 8px;\r\n  width: auto;\r\n  max-width: fit-content;\r\n  flex-shrink: 0;\r\n  margin: 6px;\r\n}\r\n\r\n.btn-small:hover {\r\n  background: rgba(71, 85, 105, 0.5);\r\n  border-color: rgba(148, 163, 184, 0.3);\r\n}\r\n\r\n/* Control Panel */\r\n.control-panel {\r\n  background: rgba(30, 41, 59, 0.4);\r\n  backdrop-filter: blur(20px);\r\n  border-right: 1px solid rgba(148, 163, 184, 0.1);\r\n  overflow: hidden;\r\n  display: flex;\r\n  flex-direction: column;\r\n  flex: 1;\r\n  min-width: 400px;\r\n  container-type: inline-size;\r\n  container-name: control-panel;\r\n}\r\n\r\n.tabs {\r\n  display: flex;\r\n  border-bottom: 1px solid rgba(148, 163, 184, 0.1);\r\n  background: rgba(15, 23, 42, 0.5);\r\n}\r\n\r\n.tab {\r\n  flex: 1;\r\n  padding: 16px 18px;\r\n  background: transparent;\r\n  border: none;\r\n  border-right: 1px solid rgba(148, 163, 184, 0.1);\r\n  border-bottom: 3px solid transparent;\r\n  cursor: pointer;\r\n  font-size: 14px;\r\n  font-weight: 600;\r\n  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);\r\n  color: #94a3b8;\r\n  white-space: nowrap;\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n}\r\n\r\n/* Container query for tabs - make them scrollable when too narrow */\r\n@container control-panel (max-width: 500px) {\r\n  .tabs {\r\n    overflow-x: auto;\r\n    -webkit-overflow-scrolling: touch;\r\n    scrollbar-width: thin;\r\n  }\r\n  \r\n  .tab {\r\n    flex: 0 0 auto;\r\n    min-width: 100px;\r\n    font-size: 12px;\r\n    padding: 12px 14px;\r\n  }\r\n}\r\n\r\n.tab:hover {\r\n  background: rgba(51, 65, 85, 0.5);\r\n  color: #e2e8f0;\r\n}\r\n\r\n.tab.active {\r\n  background: rgba(30, 41, 59, 0.8);\r\n  border-bottom-color: #3b82f6;\r\n  color: #ffffff;\r\n  box-shadow: inset 0 -3px 0 #3b82f6;\r\n}\r\n\r\n.tab-content {\r\n  padding: 24px;\r\n  overflow-y: auto;\r\n  flex: 1;\r\n  background: rgba(15, 23, 42, 0.3);\r\n}\r\n\r\n/* Info Section */\r\n.info-section h3 {\r\n  color: #6fbf73;\r\n  margin-bottom: 15px;\r\n  font-family: 'Share Tech Mono', monospace;\r\n  text-transform: uppercase;\r\n  letter-spacing: 2px;\r\n}\r\n\r\n.info-section p {\r\n  margin-bottom: 10px;\r\n  color: #b0b0b0;\r\n  font-size: 12px;\r\n}\r\n\r\n/* Command Section */\r\n.command-section h3 {\r\n  color: #ffffff;\r\n  margin-bottom: 24px;\r\n  font-weight: 700;\r\n  font-size: 18px;\r\n  border-bottom: 2px solid rgba(59, 130, 246, 0.3);\r\n  padding-bottom: 12px;\r\n  letter-spacing: -0.5px;\r\n}\r\n\r\n.command-buttons {\r\n  display: flex;\r\n  flex-wrap: wrap;\r\n  gap: 12px;\r\n}\r\n\r\n/* Section Groups */\r\n.section-group {\r\n  margin-bottom: 28px;\r\n  padding-bottom: 24px;\r\n  border-bottom: 1px solid rgba(148, 163, 184, 0.1);\r\n}\r\n\r\n.section-group:last-child {\r\n  border-bottom: none;\r\n}\r\n\r\n.section-group h4 {\r\n  color: #cbd5e1;\r\n  margin-bottom: 16px;\r\n  font-size: 15px;\r\n  font-weight: 600;\r\n  letter-spacing: -0.3px;\r\n}\r\n\r\n/* AV Capture Grid Layout */\r\n.av-capture-grid {\r\n  display: grid;\r\n  grid-template-columns: 1fr 1fr;\r\n  gap: 24px;\r\n}\r\n\r\n.av-column {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 0;\r\n}\r\n\r\n.av-column .section-group {\r\n  flex: 1;\r\n}\r\n\r\n.camera-column {\r\n  border-right: 1px solid rgba(148, 163, 184, 0.1);\r\n  padding-right: 24px;\r\n}\r\n\r\n.mic-column {\r\n  padding-left: 0;\r\n}\r\n\r\n@media (max-width: 1200px) {\r\n  .av-capture-grid {\r\n    grid-template-columns: 1fr;\r\n  }\r\n  \r\n  .camera-column {\r\n    border-right: none;\r\n    padding-right: 0;\r\n    border-bottom: 1px solid rgba(148, 163, 184, 0.1);\r\n    padding-bottom: 24px;\r\n  }\r\n}\r\n\r\n/* Process Actions Row - Horizontal Layout */\r\n.process-actions-row {\r\n  display: flex;\r\n  gap: 20px;\r\n  margin-bottom: 25px;\r\n}\r\n\r\n.half-width {\r\n  flex: 1;\r\n  min-width: 0;\r\n}\r\n\r\n/* Container query for control-panel resize */\r\n@container control-panel (max-width: 800px) {\r\n  .process-actions-row {\r\n    flex-direction: column;\r\n    gap: 15px;\r\n  }\r\n  \r\n  .command-buttons {\r\n    flex-direction: column;\r\n  }\r\n  \r\n  .command-buttons .btn {\r\n    width: 100%;\r\n    justify-content: center;\r\n  }\r\n}\r\n\r\n@container control-panel (max-width: 600px) {\r\n  .input-group {\r\n    flex-direction: column;\r\n  }\r\n  \r\n  .input-group .btn {\r\n    width: 100%;\r\n  }\r\n  \r\n  .tabs {\r\n    overflow-x: auto;\r\n    -webkit-overflow-scrolling: touch;\r\n  }\r\n  \r\n  .tab {\r\n    flex-shrink: 0;\r\n    min-width: 120px;\r\n  }\r\n}\r\n\r\n@media (max-width: 768px) {\r\n  .process-actions-row {\r\n    flex-direction: column;\r\n  }\r\n}\r\n\r\n/* Input Groups */\r\n.input-group {\r\n  display: flex;\r\n  gap: 12px;\r\n  margin-bottom: 12px;\r\n}\r\n\r\n.input-group input {\r\n  flex: 1;\r\n  padding: 12px 16px;\r\n  border: 1px solid rgba(148, 163, 184, 0.2);\r\n  border-radius: 10px;\r\n  background: rgba(15, 23, 42, 0.6);\r\n  font-size: 14px;\r\n  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);\r\n  color: #e2e8f0;\r\n}\r\n\r\n.input-group input::placeholder {\r\n  color: #64748b;\r\n}\r\n\r\n.input-group input:focus {\r\n  outline: none;\r\n  border-color: rgba(59, 130, 246, 0.5);\r\n  background: rgba(15, 23, 42, 0.8);\r\n  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);\r\n}\r\n\r\n.input-group input:disabled {\r\n  background-color: rgba(30, 41, 59, 0.5);\r\n  cursor: not-allowed;\r\n  opacity: 0.5;\r\n  border-color: rgba(100, 116, 139, 0.2);\r\n}\r\n\r\n.input-group .btn {\r\n  padding: 12px 24px;\r\n  white-space: nowrap;\r\n  flex-shrink: 0;\r\n  width: auto;\r\n}\r\n\r\n.help-text {\r\n  color: #94a3b8;\r\n  font-size: 13px;\r\n  margin: 8px 0 0 0;\r\n  line-height: 1.5;\r\n}\r\n\r\n.btn-success {\r\n  background: rgba(16, 185, 129, 0.15);\r\n  color: #6ee7b7;\r\n  border: 1px solid rgba(16, 185, 129, 0.3);\r\n  border-radius: 10px;\r\n  padding: 12px 20px;\r\n  margin: 6px;\r\n  font-size: 14px;\r\n  width: auto;\r\n  max-width: fit-content;\r\n  flex-shrink: 0;\r\n}\r\n\r\n.btn-success:hover:not(:disabled) {\r\n  background: rgba(16, 185, 129, 0.25);\r\n  border-color: rgba(16, 185, 129, 0.5);\r\n  box-shadow: 0 4px 20px rgba(16, 185, 129, 0.3);\r\n  transform: translateY(-2px);\r\n}\r\n\r\n/* Logs Panel */\r\n.logs-panel {\r\n  background: rgba(30, 41, 59, 0.4);\r\n  backdrop-filter: blur(20px);\r\n  border-left: 1px solid rgba(148, 163, 184, 0.1);\r\n  overflow: hidden;\r\n  display: flex;\r\n  flex-direction: column;\r\n  min-width: 300px;\r\n  max-width: 800px;\r\n  flex-shrink: 0;\r\n}\r\n\r\n/* Resize Handle */\r\n.resize-handle {\r\n  width: 4px;\r\n  background: rgba(148, 163, 184, 0.1);\r\n  cursor: col-resize;\r\n  position: relative;\r\n  transition: background 0.2s ease;\r\n  flex-shrink: 0;\r\n}\r\n\r\n.resize-handle:hover {\r\n  background: rgba(59, 130, 246, 0.4);\r\n}\r\n\r\n.resize-handle::after {\r\n  content: '';\r\n  position: absolute;\r\n  top: 0;\r\n  left: -4px;\r\n  width: 12px;\r\n  height: 100%;\r\n  cursor: col-resize;\r\n}\r\n\r\n.logs-header {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  padding: 16px 24px;\r\n  background: rgba(15, 23, 42, 0.5);\r\n  border-bottom: 1px solid rgba(148, 163, 184, 0.1);\r\n}\r\n\r\n.logs-header h3 {\r\n  color: #ffffff;\r\n  font-size: 16px;\r\n  font-weight: 700;\r\n  letter-spacing: -0.3px;\r\n}\r\n\r\n.logs-content {\r\n  padding: 16px;\r\n  overflow-y: auto;\r\n  flex: 1;\r\n  background: rgba(15, 23, 42, 0.3);\r\n  font-family: 'JetBrains Mono', monospace;\r\n  font-size: 13px;\r\n}\r\n\r\n.no-logs {\r\n  color: #64748b;\r\n  text-align: center;\r\n  padding: 24px;\r\n  font-size: 14px;\r\n}\r\n\r\n.log-entry {\r\n  padding: 10px 14px;\r\n  margin-bottom: 6px;\r\n  border-left: 3px solid transparent;\r\n  border-radius: 6px;\r\n  display: flex;\r\n  gap: 12px;\r\n  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);\r\n  background: rgba(15, 23, 42, 0.3);\r\n}\r\n\r\n.log-entry:hover {\r\n  background: rgba(51, 65, 85, 0.3);\r\n}\r\n\r\n.log-time {\r\n  color: #94a3b8;\r\n  font-weight: 600;\r\n  font-size: 12px;\r\n  white-space: nowrap;\r\n}\r\n\r\n.log-message {\r\n  flex: 1;\r\n  font-size: 13px;\r\n  line-height: 1.5;\r\n}\r\n\r\n.log-info {\r\n  border-left-color: #3b82f6;\r\n  color: #93c5fd;\r\n}\r\n\r\n.log-success {\r\n  border-left-color: #10b981;\r\n  color: #6ee7b7;\r\n}\r\n\r\n.log-error {\r\n  border-left-color: #ef4444;\r\n  color: #fca5a5;\r\n}\r\n\r\n.log-warning {\r\n  border-left-color: #f59e0b;\r\n  color: #fcd34d;\r\n}\r\n\r\n/* Scrollbar */\r\n.logs-content::-webkit-scrollbar,\r\n.tab-content::-webkit-scrollbar,\r\n.process-groups::-webkit-scrollbar {\r\n  width: 8px;\r\n}\r\n\r\n.logs-content::-webkit-scrollbar-track,\r\n.tab-content::-webkit-scrollbar-track,\r\n.process-groups::-webkit-scrollbar-track {\r\n  background: rgba(15, 23, 42, 0.4);\r\n}\r\n\r\n.logs-content::-webkit-scrollbar-thumb,\r\n.tab-content::-webkit-scrollbar-thumb,\r\n.process-groups::-webkit-scrollbar-thumb {\r\n  background: rgba(71, 85, 105, 0.6);\r\n  border-radius: 4px;\r\n}\r\n\r\n.logs-content::-web