 #64748b;\r\n}\r\n\r\n.login-form input:focus {\r\n  outline: none;\r\n  border-color: rgba(59, 130, 246, 0.5);\r\n  background: rgba(15, 23, 42, 0.8);\r\n  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);\r\n}\r\n\r\n.btn-login {\r\n  width: 100%;\r\n  padding: 16px;\r\n  background: linear-gradient(135deg, rgba(59, 130, 246, 0.2) 0%, rgba(37, 99, 235, 0.2) 100%);\r\n  color: #ffffff;\r\n  border: 1px solid rgba(59, 130, 246, 0.4);\r\n  border-radius: 10px;\r\n  font-size: 15px;\r\n  font-weight: 700;\r\n  cursor: pointer;\r\n  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  gap: 12px;\r\n  margin-top: 12px;\r\n}\r\n\r\n.btn-login:hover {\r\n  background: linear-gradient(135deg, rgba(59, 130, 246, 0.3) 0%, rgba(37, 99, 235, 0.3) 100%);\r\n  border-color: rgba(59, 130, 246, 0.6);\r\n  box-shadow: 0 8px 24px rgba(59, 130, 246, 0.3);\r\n  transform: translateY(-2px);\r\n}\r\n\r\n.btn-login:active {\r\n  transform: translateY(0);\r\n}\r\n\r\n.btn-icon {\r\n  font-size: 18px;\r\n}\r\n\r\n.login-info {\r\n  margin-top: 28px;\r\n  padding: 18px;\r\n  background: rgba(30, 41, 59, 0.5);\r\n  border: 1px solid rgba(148, 163, 184, 0.1);\r\n  border-left: 3px solid rgba(59, 130, 246, 0.5);\r\n  border-radius: 8px;\r\n}\r\n\r\n.login-info p {\r\n  margin: 0 0 10px 0;\r\n  color: #cbd5e1;\r\n  font-weight: 600;\r\n  font-size: 13px;\r\n}\r\n\r\n.login-info ul {\r\n  margin: 0;\r\n  padding-left: 20px;\r\n}\r\n\r\n.login-info li {\r\n  margin-bottom: 8px;\r\n  color: #94a3b8;\r\n  font-size: 13px;\r\n  line-height: 1.6;\r\n}\r\n\r\n.login-footer {\r\n  padding: 18px 28px;\r\n  background: rgba(15, 23, 42, 0.5);\r\n  text-align: center;\r\n  border-top: 1px solid rgba(148, 163, 184, 0.1);\r\n}\r\n\r\n.login-footer p {\r\n  margin: 0;\r\n  color: #64748b;\r\n  font-size: 13px;\r\n}\r\n\r\n/* ============================================\r\n   KEYLOGGER DUAL PANEL STYLES\r\n   ============================================ */\r\n\r\n.keylog-display-dual {\r\n  display: grid;\r\n  grid-template-columns: 1fr 1fr;\r\n  gap: 20px;\r\n  margin-top: 20px;\r\n}\r\n\r\n.keylog-panel {\r\n  background: rgba(15, 23, 42, 0.6);\r\n  border: 1px solid rgba(148, 163, 184, 0.2);\r\n  border-radius: 12px;\r\n  overflow: hidden;\r\n  backdrop-filter: blur(10px);\r\n}\r\n\r\n.keylog-panel h4 {\r\n  margin: 0;\r\n  padding: 14px 18px;\r\n  background: rgba(30, 41, 59, 0.8);\r\n  border-bottom: 1px solid rgba(148, 163, 184, 0.2);\r\n  color: #e2e8f0;\r\n  font-size: 14px;\r\n  font-weight: 700;\r\n  letter-spacing: 0.3px;\r\n}\r\n\r\n.keylog-panel .keylog-content {\r\n  padding: 16px;\r\n  height: 400px;\r\n  overflow-y: auto;\r\n  overflow-x: hidden;\r\n}\r\n\r\n.keylog-panel .keylog-content pre {\r\n  margin: 0;\r\n  font-family: 'JetBrains Mono', monospace;\r\n  font-size: 13px;\r\n  line-height: 1.8;\r\n  color: #cbd5e1;\r\n  white-space: pre-wrap;\r\n  word-wrap: break-word;\r\n  word-break: break-all;\r\n}\r\n\r\n/* Scrollbar styling for keylog panels */\r\n.keylog-panel .keylog-content::-webkit-scrollbar {\r\n  width: 8px;\r\n}\r\n\r\n.keylog-panel .keylog-content::-webkit-scrollbar-track {\r\n  background: rgba(15, 23, 42, 0.3);\r\n  border-radius: 4px;\r\n}\r\n\r\n.keylog-panel .keylog-content::-webkit-scrollbar-thumb {\r\n  background: rgba(148, 163, 184, 0.3);\r\n  border-radius: 4px;\r\n}\r\n\r\n.keylog-panel .keylog-content::-webkit-scrollbar-thumb:hover {\r\n  background: rgba(148, 163, 184, 0.5);\r\n}\r\n\r\n/* Keylog mode selector */\r\n.keylog-mode-selector {\r\n  margin-bottom: 20px;\r\n  padding: 16px;\r\n  background: rgba(30, 41, 59, 0.5);\r\n  border: 1px solid rgba(148, 163, 184, 0.2);\r\n  border-radius: 12px;\r\n}\r\n\r\n.keylog-mode-selector label {\r\n  display: block;\r\n  margin-bottom: 10px;\r\n  color: #e2e8f0;\r\n  font-weight: 600;\r\n  font-size: 14px;\r\n}\r\n\r\n.mode-buttons {\r\n  display: flex;\r\n  gap: 12px;\r\n  margin-bottom: 12px;\r\n}\r\n\r\n.btn-mode {\r\n  flex: 1;\r\n  padding: 12px 20px;\r\n  background: rgba(15, 23, 42, 0.6);\r\n  color: #cbd5e1;\r\n  border: 1px solid rgba(148, 163, 184, 0.2);\r\n  border-radius: 8px;\r\n  font-size: 14px;\r\n  font-weight: 600;\r\n  cursor: pointer;\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.btn-mode:hover {\r\n  background: rgba(30, 41, 59, 0.8);\r\n  border-color: rgba(148, 163, 184, 0.4);\r\n  transform: translateY(-2px);\r\n}\r\n\r\n.btn-mode.active {\r\n  background: linear-gradient(135deg, rgba(59, 130, 246, 0.2) 0%, rgba(37, 99, 235, 0.2) 100%);\r\n  color: #60a5fa;\r\n  border-color: rgba(59, 130, 246, 0.5);\r\n  box-shadow: 0 4px 16px rgba(59, 130, 246, 0.2);\r\n}\r\n\r\n.mode-description {\r\n  margin: 0;\r\n  color: #94a3b8;\r\n  font-size: 13px;\r\n  font-style: italic;\r\n}\r\n\r\n/* Responsive design for dual panel */\r\n@media (max-width: 1200px) {\r\n  .keylog-display-dual {\r\n    grid-template-columns: 1fr;\r\n  }\r\n}\r\n\r\n/* ============================================\r\n   CUSTOM MODAL STYLES\r\n   ============================================ */\r\n\r\n.modal-overlay {\r\n  position: fixed;\r\n  top: 0;\r\n  left: 0;\r\n  right: 0;\r\n  bottom: 0;\r\n  background: rgba(0, 0, 0, 0.75);\r\n  backdrop-filter: blur(8px);\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  z-index: 10000;\r\n  animation: fadeIn 0.2s ease;\r\n}\r\n\r\n@keyframes fadeIn {\r\n  from {\r\n    opacity: 0;\r\n  }\r\n  to {\r\n    opacity: 1;\r\n  }\r\n}\r\n\r\n.modal-box {\r\n  background: linear-gradient(135deg, rgba(30, 41, 59, 0.98) 0%, rgba(51, 65, 85, 0.98) 100%);\r\n  border: 1px solid rgba(148, 163, 184, 0.3);\r\n  border-radius: 16px;\r\n  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);\r\n  min-width: 400px;\r\n  max-width: 500px;\r\n  animation: slideIn 0.3s ease;\r\n  overflow: hidden;\r\n}\r\n\r\n@keyframes slideIn {\r\n  from {\r\n    transform: scale(0.9) translateY(-20px);\r\n    opacity: 0;\r\n  }\r\n  to {\r\n    transform: scale(1) translateY(0);\r\n    opacity: 1;\r\n  }\r\n}\r\n\r\n.modal-header {\r\n  padding: 20px 24px;\r\n  background: rgba(15, 23, 42, 0.8);\r\n  border-bottom: 1px solid rgba(148, 163, 184, 0.2);\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: space-between;\r\n}\r\n\r\n.modal-header h3 {\r\n  margin: 0;\r\n  color: #e2e8f0;\r\n  font-size: 18px;\r\n  font-weight: 700;\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 10px;\r\n}\r\n\r\n.modal-close {\r\n  background: none;\r\n  border: none;\r\n  color: #94a3b8;\r\n  font-size: 24px;\r\n  cursor: pointer;\r\n  padding: 0;\r\n  width: 32px;\r\n  height: 32px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  border-radius: 6px;\r\n  transition: all 0.2s ease;\r\n}\r\n\r\n.modal-close:hover {\r\n  background: rgba(239, 68, 68, 0.2);\r\n  color: #fca5a5;\r\n}\r\n\r\n.modal-body {\r\n  padding: 28px 24px;\r\n}\r\n\r\n.modal-body p {\r\n  margin: 0;\r\n  color: #cbd5e1;\r\n  font-size: 15px;\r\n  line-height: 1.8;\r\n  white-space: pre-line;\r\n}\r\n\r\n.modal-footer {\r\n  padding: 16px 24px;\r\n  background: rgba(15, 23, 42, 0.5);\r\n  border-top: 1px solid rgba(148, 163, 184, 0.2);\r\n  display: flex;\r\n  gap: 12px;\r\n  justify-content: flex-end;\r\n}\r\n\r\n.btn-modal {\r\n  padding: 10px 24px;\r\n  border-radius: 8px;\r\n  font-size: 14px;\r\n  font-weight: 600;\r\n  cursor: pointer;\r\n  transition: all 0.3s ease;\r\n  border: 1px solid;\r\n  min-width: 80px;\r\n}\r\n\r\n.btn-ok {\r\n  background: linear-gradient(135deg, rgba(59, 130, 246, 0.2) 0%, rgba(37, 99, 235, 0.2) 100%);\r\n  color: #60a5fa;\r\n  border-color: rgba(59, 130, 246, 0.4);\r\n}\r\n\r\n.btn-ok:hover {\r\n  background: linear-gradient(135deg, rgba(59, 130, 246, 0.3) 0%, rgba(37, 99, 235, 0.3) 100%);\r\n  border-color: rgba(59, 130, 246, 0.6);\r\n  box-shadow: 0 4px 16px rgba(59, 130, 246, 0.3);\r\n  transform: translateY(-2px);\r\n}\r\n\r\n.btn-cancel {\r\n  background: rgba(51, 65, 85, 0.5);\r\n  color: #94a3b8;\r\n  border-color: rgba(148, 163, 184, 0.3);\r\n}\r\n\r\n.btn-cancel:hover {\r\n  background: rgba(51, 65, 85, 0.8);\r\n  border-color: rgba(148, 163, 184, 0.5);\r\n  color: #cbd5e1;\r\n}\r\n\r\n.btn-confirm {\r\n  background: linear-gradient(135deg, rgba(239, 68, 68, 0.2) 0%, rgba(220, 38, 38, 0.2) 100%);\r\n  color: #fca5a5;\r\n  border-color: rgba(239, 68, 68, 0.4);\r\n}\r\n\r\n.btn-confirm:hover {\r\n  background: linear-gradient(135deg, rgba(239, 68, 68, 0.3) 0%, rgba(220, 38, 38, 0.3) 100%);\r\n  border-color: rgba(239, 68, 68, 0.6);\r\n  box-shadow: 0 4px 16px rgba(239, 68, 68, 0.3);\r\n  transform: translateY(-2px);\r\n}\r\n\r\n.btn-modal:active {\r\n  transform: translateY(0);\r\n}\r\n\r\n/* Modal responsive */\r\n@media (max-width: 600px) {\r\n  .modal-box {\r\n    min-width: 90%;\r\n    max-width: 90%;\r\n  }\r\n}\r\n\r\n/* ============================================\r\n   CMD TERMINAL STYLES\r\n   ============================================ */\r\n\r\n.terminal-controls {\r\n  margin-bottom: 20px;\r\n  padding: 16px;\r\n  background: rgba(30, 41, 59, 0.5);\r\n  border: 1px solid rgba(148, 163, 184, 0.2);\r\n  border-radius: 12px;\r\n}\r\n\r\n.control-row {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: space-between;\r\n  margin-bottom: 12px;\r\n}\r\n\r\n.checkbox-label {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 10px;\r\n  color: #cbd5e1;\r\n  font-weight: 600;\r\n  cursor: pointer;\r\n  padding: 8px 16px;\r\n  background: rgba(71, 85, 105, 0.3);\r\n  border: 1px solid rgba(148, 163, 184, 0.2);\r\n  border-radius: 10px;\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.checkbox-label:hover {\r\n  background: rgba(71, 85, 105, 0.5);\r\n  border-color: rgba(148, 163, 184, 0.4);\r\n}\r\n\r\n.checkbox-label input[type=\"checkbox\"] {\r\n  width: 18px;\r\n  height: 18px;\r\n  cursor: pointer;\r\n  accent-color: #3b82f6;\r\n}\r\n\r\n.checkbox-label span {\r\n  font-size: 14px;\r\n  letter-spacing: 0.3px;\r\n  user-select: none;\r\n  white-space: nowrap;\r\n}\r\n\r\n.status-badge {\r\n  padding: 8px 16px;\r\n  background: rgba(15, 23, 42, 0.6);\r\n  border: 1px solid rgba(148, 163, 184, 0.2);\r\n  border-radius: 8px;\r\n  color: #94a3b8;\r\n  font-size: 14px;\r\n  font-weight: 600;\r\n}\r\n\r\n.status-running {\r\n  color: #4ade80;\r\n}\r\n\r\n.status-stopped {\r\n  color: #f87171;\r\n}\r\n\r\n.button-row {\r\n  display: flex;\r\n  gap: 12px;\r\n  flex-wrap: wrap;\r\n}\r\n\r\n/* Container query for button rows and upload controls */\r\n@container control-panel (max-width: 750px) {\r\n  .button-row {\r\n    flex-direction: column;\r\n  }\r\n  \r\n  .button-row .btn {\r\n    width: 100%;\r\n  }\r\n  \r\n  .upload-controls {\r\n    flex-direction: column;\r\n    align-items: stretch;\r\n  }\r\n  \r\n  .upload-controls .btn {\r\n    width: 100%;\r\n  }\r\n  \r\n  .file-name {\r\n    width: 100%;\r\n    text-align: center;\r\n  }\r\n}\r\n\r\n.btn-success {\r\n  background: linear-gradient(135deg, rgba(34, 197, 94, 0.2) 0%, rgba(22, 163, 74, 0.2) 100%);\r\n  color: #4ade80;\r\n  border: 1px solid rgba(34, 197, 94, 0.4);\r\n}\r\n\r\n.btn-success:hover:not(:disabled) {\r\n  background: linear-gradient(135deg, rgba(34, 197, 94, 0.3) 0%, rgba(22, 163, 74, 0.3) 100%);\r\n  border-color: rgba(34, 197, 94, 0.6);\r\n  box-shadow: 0 4px 16px rgba(34, 197, 94, 0.3);\r\n}\r\n\r\n.btn-warning {\r\n  background: linear-gradient(135deg, rgba(234, 179, 8, 0.2) 0%, rgba(202, 138, 4, 0.2) 100%);\r\n  color: #facc15;\r\n  border: 1px solid rgba(234, 179, 8, 0.4);\r\n  margin: 6px;\r\n  max-width: fit-content;\r\n}\r\n\r\n.btn-warning:hover:not(:disabled) {\r\n  background: linear-gradient(135deg, rgba(234, 179, 8, 0.3) 0%, rgba(202, 138, 4, 0.3) 100%);\r\n  border-color: rgba(234, 179, 8, 0.6);\r\n  box-shadow: 0 4px 16px rgba(234, 179, 8, 0.3);\r\n}\r\n\r\n.btn-danger {\r\n  background: linear-gradient(135deg, rgba(239, 68, 68, 0.2) 0%, rgba(220, 38, 38, 0.2) 100%);\r\n  color: #fca5a